import React, { Component } from "react";
import "./style.css";

export default class game extends Component {
  state = {
    urlArr: [
      "https://remicy.itch.io/44-room",
      "https://remicy.itch.io/abandoned-planet",
      "https://remicy.itch.io/lifenodes",
      "https://remicy.itch.io/legend-of-destiny",
      "https://remicy.itch.io/lab-493",
      "https://remicy.itch.io/shift-heroes",
      "https://remicy.itch.io/baa-baa-black-sheep",
      "https://remicy.itch.io/pyrite"
    ]
  };
  openURL = e => {
    window.open(this.state.urlArr[parseInt(e.target.id) - 1], "_blank");
  };
  render() {
    return (
      <div className="games">
        <div>
          <img className="img" src="img/gamed.png" />
          <br />
        </div>
        <div className="gamezone">
          <div>
            <img
              src="img/44.png"
              id="1"
              className="game"
              onClick={this.openURL}
            />
            <br />
            遊戲名稱：4/4 Room
            <br /> 官方主題：One Room <br />
            成績：Overall (#28)、Fun (#27)
          </div>
          <div>
            <img
              src="img/ap.jpg"
              id="2"
              className="game"
              onClick={this.openURL}
            />
            <br />
            遊戲名稱：Abandoned Planet
            <br /> 官方主題：Ancient Technology <br />
            成績：此次比賽未計分
          </div>
          <div>
            <img
              src="img/lifenodes.jpg"
              id="3"
              className="game"
              onClick={this.openURL}
            />
            <br />
            遊戲名稱：Lifenodes
            <br /> 官方主題：Growing <br />
            成績：Mood (#13)、Overall (#72)、
            <br />
            Graphics (#58)
            <br />
          </div>
          <div>
            <img
              src="img/lod.png"
              id="4"
              className="game"
              onClick={this.openURL}
            />
            <br />
            遊戲名稱：Legend of Destiny
            <br /> 此遊戲為自主開發，
            <br />
            未來想要做成手機遊戲的 prototype
            <br />
          </div>
          <div>
            <img
              src="img/493.png"
              id="5"
              className="game"
              onClick={this.openURL}
            />
            <br />
            遊戲名稱：Lab 493
            <br />
            官方主題：A Small World
            <br />
            成績：Audio (#88)、Overall (#276)
            <br />
          </div>
          <div>
            <img
              src="img/sh.jpg"
              id="6"
              className="game"
              onClick={this.openURL}
            />
            <br />
            遊戲名稱：ShiftHeroes
            <br /> 官方主題：Shapeshift <br />
            成績：Theme (#102)、Overall (#188)
            <br />
          </div>
          <div>
            <img
              src="img/baa.png"
              id="7"
              className="game"
              onClick={this.openURL}
            />
            <br />
            遊戲名稱：Baa Baa Black Sheep
            <br /> 自主開發
            <br />
          </div>
          <div>
            <img
              src="img/pyrite.png"
              id="8"
              className="game"
              onClick={this.openURL}
            />
            <br />
            遊戲名稱：Pyrite
            <br /> 早期開發練習
            <br />
          </div>
        </div>
      </div>
    );
  }
}
